:root{
    --size:min(70vh, 70vw);
    --font-size:min(10vh, 10vw);
}

body{
    offset: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    overflow: none;
}

.app {
    display: flex;
    flex-direction: column;
    height: var(--size);
    width: var(--size);
    background-color: black;
}

#output{
    background-color: wheat;
    font-size: var(--font-size);
    min-height: var(--font-size);
    overflow-x:scroll;
}

#middle {
    height: 100%;
    display: grid;
    grid-template-columns: 3fr 2fr;
    gap: 10px;
    background-color: black;
}

#operations{
    display:grid;
    grid-auto-flow: column;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(2, 1fr);
    gap:3px;
}

#numpad {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5px;
}

#numpad>div:last-child {
    grid-column: 2/3;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size);
    background-color: gray;
    user-select: none;
}

.button:hover {
    background-color: darkgray;
    cursor: pointer;
}

.button:active{
    background-color: red;
}